import { environment } from '@ali/4ever-cangjie';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';
const OPTION = environment.IS_MAC ? 'Option' : 'Alt';

export default function getTipsByHeading(heading: string, locale, withoutTitle?: boolean) {
  if (!heading) return locale.dropdownParagraph;
  const regExp = /(\d)/;
  const matchRes = heading?.match(regExp);
  const level = matchRes ? matchRes[1] : 0;
  let buttonTiltle;
  if (level === 0) {
    buttonTiltle = locale.dropdownParagraph;
  } else {
    buttonTiltle = `${locale.buttonTitle}${level}`;
  }
  return `${withoutTitle ? '' : buttonTiltle + ' '}${MOD}+${OPTION}+${level}`;
}

export function getTitleByHeading(heading: string, locale): { title: string, shortCut: string, markdown?: string } {
  if (!heading) return locale.dropdownParagraph;
  const regExp = /(\d)/;
  const matchRes = heading?.match(regExp);
  const level = matchRes ? matchRes[1] : 0;
  let buttonTiltle;
  if (level === 0) {
    buttonTiltle = locale.dropdownParagraph;
  } else {
    buttonTiltle = `${locale.buttonTitle}${level}`;
  }
  return {
    title: buttonTiltle,
    shortCut: `${MOD}+${OPTION}+${level}`,
    markdown: level ? 'Markdown: ' + '#'.repeat(Number(level)) + ' ' + (locale?.space || 'Space') : undefined,
  }
}
